/** @jsx jsx */
import { jsx } from "theme-ui"
import { rem } from "@src/theme"
import styled from "styled-components"

const BlendedImage = ({ image, width, height, border = true }) => (
  <div
    sx={{
      bg: "orange",
      width,
      height,
      maxWidth: "100%"
    }}
  >
    <div
      className="blended-image"
      sx={{
        backgroundImage: `url(${image})`,
        mixBlendMode: "multiply",
        filter: "grayscale(100%)",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        width: "100%",
        height: "100%",
        borderRadius: rem(12),
        border: border ? "13px solid black" : "none",
        "&:hover, &:focus, &:focus-within": {
          filter: "none",
          outline: "none"
        }
      }}
    ></div>
  </div>
)

export const WithLink = props => (
  <LinkWrapper
    href={props.link}
    target="_blank"
    sx={{
      display: "inline-block",
      width: "100%"
    }}
  >
    <BlendedImage {...props} />
  </LinkWrapper>
)

export default BlendedImage

const LinkWrapper = styled.a`
  &:hover,
  &:focus,
  &:focus-within {
    outline: none;
    .blended-image {
      filter: none;
      outline: none;
    }
  }
`
